import { useEffect, useState } from "react";
import Header from "../../components/header/Header";
import Posts from "../../components/posts/Posts";
import Sidebar from "../../components/sidebar/Sidebar";
import Select from "react-select";
import "./home.css";
import axios from "axios";
import { Link } from "react-scroll";
import { useLocation } from "react-router";
import * as actions from "../../redux/actions";
import PortFolio from "../../components/PortFolio/PortFolio";
import imagePath from "../../constant/imagePath";

const options = [
  { value: "chocolate", label: "Travel" },
  { value: "strawberry", label: "Religious" },
  { value: "vanilla", label: "Entertainment" },
  { value: "chocolate", label: "Kids and extra" },
  { value: "strawberry", label: "Painting" },
  { value: "vanilla", label: "Serious" },
];

export default function Home() {
  const [posts, setPosts] = useState([]);
  const [skip, setSkip] = useState(0);
  const [isMoreContent, setIsMoreContent] = useState(true);
  const [category, setCategory] = useState([]);
  const [categoryRequested, setCategoryRequested] = useState("");
  const { search } = useLocation();

  useEffect(() => {
    const initialization = async () => {
      if (isMoreContent) {
        try {
          let query = `?offset=${skip}&limit=10&category=${categoryRequested}`;
          const blogPostResult = await actions.getBlogPost(query);
          if (skip == 0) setPosts(blogPostResult);
          else setPosts([...category, ...blogPostResult.results]);
          if (blogPostResult.length < 10) setIsMoreContent(false);
        } catch (error) {
          console.log(error, "this is the eror ");
        }
      }
    };
    initialization();
  }, [search, skip, categoryRequested]);
  useEffect(() => {
    const initialization = async () => {
      const categoryResult = await actions.getCategory();
      setCategory(categoryResult.results);
    };
    initialization();
  }, [search]);
  return (
    <>
      <Header />
      <div className="recent-block">
        <div className="recent-block-img">
          <img src={imagePath.recent3} className="recent-img1" />
          <img className="recent-img2" src={imagePath.recent2} />
          <img className="recent-img3" src={imagePath.recent1} />
        </div>
        <div className="recent-block-text">
          <img
            src={imagePath.about}
            style={{
              width: 100,
              height: 100,
              zIndex: 1,
            }}
          />
          <p style={{ fontFamily: "fantasy" }}>
            <span style={{ fontWeight: "bold" }}>5000+ hours</span> of popular
            Indian and International shows across{" "}
            <span style={{ fontWeight: "bold" }}>200+ category</span>
          </p>
          <Select options={options} />
        </div>
      </div>
      <div className="home">
        <Posts posts={posts} />
      </div>
      <div className="about-block">
        <div className="about-img">
          <img
            src={imagePath.profilePic}
            style={{
              width: 300,
              height: 300,
              zIndex: 1,
            }}
          />
        </div>
        <div
          className="about-me"
          style={{ paddingLeft: 24, textAlign: "justify", letterSpacing: 2 }}
        >
          <div className="about-header">
            <img
              src={imagePath.aboutDialog}
              style={{
                alignContent: "center",
                // position: "absolute",
                // bottom: 0,
                width: 200,
                height: 100,
                zIndex: 1,
              }}
            />
          </div>
          <p style={{ lineHeight: 1.5, fontFamily: "cursive" }}>
            Born in Maharashtra, Bharat [India], Nilesh Chandrakant Nevgi is a
            bachelor of fine arts from Sir J J School of Art, Mumbai. He is an
            IP creator, director and producer. In the last 20 years He has
            directed shorts ,films and series with various Indian animation
            companies. He has a number of well-regarded works to his credit,such
            as Pakdam Pakdai,Motu Patlu,Vir the ROBOT BOY,Shiva,Chloe's
            Closet,Growing Up Creepie,Mucha Lucha,Meena [UNICEF ] to name a few.
            To explore his own storytelling he has founded SCNN Productions. In
            2008 'H2O' an animation short film under SCNN Productions was
            awarded Best Screenplay at Twilight 2008 Delhi. The same film has
            been selected and shown in MIFF 2008, JIFF 2008 and also had a
            Special Screening at ART FILM FESTIVAL Sir J J School of Art 2012.
            'THE SILENT SCREAM ' Another film from SCNN Productions has got
            recognition and awards in 45+ Film festivals world-wide. ...
          </p>
        </div>
      </div>
    </>
  );
}
